import "./styles.css";

let userInput: number = Number(prompt("ingrese un numero:")); //numero ingresado por user en prompt
let maxNum: number = userInput; //variable modificada por numero maximo
let minNum: number = maxNum; //variable modificada por numero minimo

//codigo de modificacion de variables

while (userInput != 0) {
  userInput = Number(prompt("ingrese un numero:"));
  if (userInput > maxNum) {
    maxNum = userInput; //modificacion de variable maxima
  } else if (userInput < minNum && userInput != 0) {
    minNum = userInput; //modificacion de variable minima
  }
}

//resultado en consola de nuimeros recordados

console.log(
  `El numero maximo encontrado es: ${maxNum} y el numero minimo es ${minNum}`
);
